.content {
	max-width: 1240px; 
	width: 80%; 
	margin: 0 auto; 
	background-color: #131316;
	display: flex;
	gap: 40px;
	text-align: center;
}

.contentleft {
	flex-basis:50%;
	background-color: #131316;
	display: flex;
	flex-direction: column;
	height: 80vh;
}

.modal{
	width: 100%;
	display: flex;
	justify-content: center;
	padding-top: 120px;
}
